const app = getApp();
const common = require('../../../publicjs/common.js');
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
export default {
	components: {
		uniNavBar
	},
	data() {
		return {};
	},
	onLoad: () => {
		common.fonts(); //网络字体
	},
	methods: {
		//返回
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		getUserInfo(res) {
			//获取用户信息
			console.log(res);
			let iv = res.detail.iv;
			let encryptedData = res.detail.encryptedData;
			uni.login({
				provider: 'weixin',
				success: res => {
					let code = res.code;
					common
						.requestURL({
							url: '/user/wxlogin',
							method: 'POST',
							params: JSON.stringify({ code, encryptedData, iv })
						})
						.then(res => {
							console.log(res);
							if (res.code === 200) {
								uni.setStorage({key: 'token',data: res.data.token});
								uni.setStorage({key: "userinfolist",data: res.data.user});
								
								uni.redirectTo({
									url: "../index/index"
								})
							}else {
								uni.showModal({
									title: '提示',
									content: '登录失败,请重试！',
								})
							}
						});
				},
				fail: res => {
					console.log('拒绝了');
				}
			});
		}
	}
};
